<template>
  <div id="drag-and-drop">
    <div class="container">
      <div class="dropboxes-container">
        <div class="tabs">
          <tab class="tab-active" @click.native.self="clickHandler($event)" title="Build"></tab>
          <tab @click.native.self="clickHandler($event)" title="Premade"></tab>      
        </div>
        <div v-if="showBuilt" class="boxes-wrapper">
          <dropableBox v-for="box in built" :key="box.id" v-bind:boxData="box"></dropableBox>
        </div>
        <div v-else class="boxes-wrapper">
         <dropableBox v-for="box in premade" :key="box.id" v-bind:boxData="box"></dropableBox>
         </div>
      </div>
      <div class="content-wrapper">
        <pre v-highlightjs="sourcecode" @input="updateText($event)"><code contenteditable="true"  id="main-area" class="vbscript">'This code says, If Cell(A1) > 100 Then, Do Something...
</code></pre>
        <div class="action-buttons-wrapper">
          <customBtn
            class="action-btn red"
            v-bind:buttonData="actionButtons.undo"
            @click.native="undoDrop"
          ></customBtn>
          <customBtn
            class="action-btn red"
            v-bind:buttonData="actionButtons.copy"
            @click.native="copyToClipboard"
          ></customBtn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
