	.Interior.Color = RGB(0,0,255)
End With'`
                },
               
               
               
              ]
            }
          ]
        },
         {
          title: "If",
          value:
            `'This code says, If Cell(A1) > 100 Then, Do Something...` +
            `If Range("A1") = 100 Then\n` +
            `    'Your Code goes here\n` +
            `End If\n`,
          subBoxes: [
             {
              title: "Simple If",
              value:
                `'This code says, If Cell(A1) > 100 Then, Do Something...
If Range("A1") > 100 Then
    'Place Your Code Here
End if`
            },
             {
              title: "If And",
              value:
                `'This code says, If Cell(A1) > 100 And Cell B1 = Minnesota Then, Do Something...\n` +
                `If Range("A1") > 100 And Range("B1") = "Minnesota" Then\n` +
                `        'Place Your Code Here\n` +
                `End if\n
                <br/>`
            },
             {
