x = 0
While x < 5
    Range("A" & x) = " x is less than 5 "
Wend`
            },
            
          ]
        },
         {
          title: "Copy/Paste",
          value: ``,
          subBoxes: [
             {
              title: "Within Sheet",
              value:
                `'Copy the value in cell A1 and paste to cell C1\n` +
                `Range("C1") = Range("A1").Value\n`
            },
             {
              title: "Sheet to Sheet",
              value:
                `'Copy the value on Sheet2 in Cell A1 and paste to Sheet1 cell A1\n` +
                `Sheet1.Range("A1") = Sheet2.Range("A1")\n`
            },
            {
              title: 'Between Workbooks',
              value: `'Copy the value in wWrkbookA on Sheet2 in Cell A1 and paste to WorkbookB Sheet1 cell A1
Workbooks("WorkbookB.xlsx").Sheet1.Range("A1") = Workbooks("WorkbookA.xlsx")Sheet2.Range("A1")`
            }
          ]
        },
        {
