Application.ScreenUpdating = False

'Enable screen updating (place at end of code)
Application.ScreenUpdating = True`,
            },
            {
              title: 'Automatic Calculations',
              value: `'Turn automatic calculations off. (Place at beginning of code)
Application.Calculation = xlManual

'Turn on automatic calculations. (Place at end of code)
Application.Calculation = xlAutomatic`
            },
            {
              title: 'Status Bar',
              value: `'Speeds up code by disabling status bar (place at beginning of code)
Application.DisplayStatusBar = False

'Enable Status Bar (place at end of code)
Application.DisplayStatusBar = True`
            }
          ]
        },
         {
          title: "Special",
          value: "",
          subBoxes: [
            {
              title: "Find Last Row",
