dim lcol as long
lcol = Cells(1, Columns.Count).End(xltoLeft).Column`
            },
            {
              title: 'Remove Duplicates (1 Column)',
              value: `'Remove duplicates from Range A1 to A100
'Columns:=1 means that you are removing duplicates if they are in 1st column of the range (A)
'Header:=Yes means that your range includes headers
Range("A$1:$A$100").RemoveDuplicates Columns:=1, Header:=xlYes`
            },
            {
              title: `Remove Duplicates \n (2 Columns)`,
              value: `'Remove duplicates from Range A1 to B100
'Columns:=Array(1, 2), means that you are removing duplicates if they are in the 1st and 2nd column of the range (A:B)
'Header:=Yes means that your range includes headers
ActiveSheet.Range("$A$1:$B$100$").RemoveDuplicates Columns:=Array(1, 2), Header:=xlYes`
            }
            

          ]
        },
        
        
         {
           title: "Formulas",
           value: "",
           subBoxes: [
             {
               title: 'If',
               value: `'If Statement placed in range B2:B100. To place in a cell, replace B2:B100 with B2.
'(RC1>5) means that if the current row in column 1 is greater than 5, then...
'If true, then say "Greater than 5".
'If fales, then say nothing (blank)
Range("B2:B100").FormulaR1C1 = "=IF(RC1>5,""Greater than 5"","""")"`,
             },
             {
               title: 'Sumifs',
               value: `'Place Sumifs equation in cell E2
'Sum Range is C1, known as Column 1 or Column A
'Criteria Range 1 is C2, known as Column 2, or Column B.
'"X" is the criteria. The equation will sum up Column A when Column B = X.
Range("E2").FormulaR1C1 = "=SUMIFS(C1,C2,""X"")"`
             },
             {
               title: 'Vlookup',
               value: `'Vlookup placed in range E2. To place formula in Range, replace E2 with E2:E5
'(RC4) lookup is in the current row and fourth column in. (D2)
