'(R1C1:R11C2) table is in range (A1:B11)
'0 means that you are looking for an exact match
Range("C2:C10").FormulaR1C1 = "=MATCH(RC2,R1C1:R11C1,0)"`
             },
             {
               title: 'Index Match',
               value: `'Place Index Match in cell F3
'Index/Table is R1C1:R11C4 (A1:D11)
'MATCH: Row_Num Value is in cell RC5 (E3)
'MATCH: Row_Num Lookup Table is R1C11:R11C1 (A1:A11)
'Exact Match (0)
'Return the value in the second column of the Index table (2)
Range("F3").FormulaR1C1 = "=INDEX(R1C1:R11C4,MATCH(RC5,R1C1:R11C1,0),2,1)"`
             },
             {
               title: 'Index Match Match',
               value: `'Place Index Match in cell F3
