'MATCH1: Row_Num Match Value is in cell RC5 (E3)
'MATCH1: Row_Num Lookup Table is R1C11:R11C1 (A1:A11)
'Exact Match (0)
'MATCH2: Column_Num Match Value is in cell R2C (F2)
'MATCH2: Column_Num Lookup Table is R1C1:R11C4 (A1:D1)
'Exact Match (0)
Range("F4").FormulaR1C1 = "=INDEX(R1C1:R11C4,MATCH(RC5,R1C1:R11C1,0),MATCH(R2C,R1C1:R1C4,0),1)"`
             }
           ]
         },
         {
           title: 'Combinations',
           value: '',
           subBoxes: [
             {
               title: 'Loop & If & Copy/Paste',
               value: `'The code below will copy and paste Columns A:C to Columns E:G when A = Minnesota
'Used: screenupdating, find last row, for loop, if statement, and copy paste
'Speeds up code by disabling screen updating (place at beginning of code)
Application.ScreenUpdating = False
'Finds the last row of Column A. The number 1 = A, 2 = B, 3 = C...
Dim lrow As Long
lrow = Cells(Rows.Count, 1).End(xlUp).Row
'Create For Loop through 1 to lrow. My_row will equal a row number
For my_row = 1 To lrow
    'If Cell A & current row = Minnesota Then, Do Something...
    If Range("A" & my_row) = "Minnesota" Then
        'Finds the last row of Column E. The number 1 = A, 2 = B, 3 = C...
        Dim lrow As Long
        lrow = Cells(Rows.Count, 5).End(xlUp).Row
        'Copy and Paste the value from columns A:C to E:G
        Range("E" & lrow2 & ":G" & lrow2) = Range("A" & x & ":C" & x).Value
    End If
Next x
'Speeds up code by disabling screen updating (place at end of code)
Application.ScreenUpdating = True`
             }
           ]
