'Create a For Loop to go from the last row to the first row of the active sheet
For my_row = lrow To 1 Step -1
    'Counts if there are any values in the current row in the loop
    count_row = Application.WorksheetFunction.CountA(Rows(my_row))
    'If there are no values in the current row (count_row) then do something    
    If count_row = 0 Then
        'Delete the current row in the loop (my_row)
        Rows(my_row).Delete
    End If
'Go to the next iteration of the loop
Next my_row
'Speeds up code by enabling screen updating (place at end of code)
Application.ScreenUpdating = True <br>

`
            },
            {
              title: 'Blank in Workbook',
              value: `'Delete All Blank Rows from the active workbook
''Speeds up code by disabling screen updating (place at beginning of code)
Application.ScreenUpdating = False
'Counts all of the sheets in the Active Workbook
Sht_Count = Sheets.Count
For sht = 1 to Sht_Count
    'Selects the worksheet 1 through the number of worksheets you have
    Worksheets(sht).select
    'Finds last row of Column A (1) in the active sheet
    lrow = Cells(Rows.Count, 1).End(xlUp).Row
    'Create a For Loop to go from the last row to the first row of the active sheet
    For my_row = lrow To 1 Step -1
        'Counts if there are any values in the current row in the loop
        count_row = Application.WorksheetFunction.CountA(Rows(my_row))
        'If there are no values in the current row (count_row) then do something    
        If count_row = 0 Then
            'Delete the current row in the loop (my_row)
            Rows(x).Delete
        End If
    Next my_row
Next sht
'Speeds up code by enabling screen updating (place at end of code)
Application.ScreenUpdating = True`
            },
            {
              title: 'Blank in Folder',
              value: `MyFolder = .SelectedItems(1)
End With
'Speeds up code by disabling screen updating (place at beginning of code)
Application.ScreenUpdating = False
'Make the file a vb read only. (Makes code run faster)
MyFile = Dir(MyFolder & "\", vbReadOnly)
'Run the code as long as there is a file name
Do While MyFile <> ""
    'Do not update links (if there are any). Speeds up code.
    Workbooks.Open Filename:=MyFolder & "\" & MyFile, UpdateLinks:=False
    'Counts all of the sheets in the Active Workbook
    Sht_Count = Sheets.Count
    'Creates a loop for the first to the last sheet of the workbook
    For sht = 1 To Sht_Count
        'Selects the current worksheet # (sht) worksheet in the workbook
        Worksheets(sht).Select
        'Finds last row of Column A (1) in the active sheet
        lrow = Cells(Rows.Count, 1).End(xlUp).Row
        'Create a For Loop to go from the last row to the first row of the active sheet
        For my_row = lrow To 1 Step -1
            'Counts if there are any values in the current row in the loop
            count_row = Application.WorksheetFunction.CountA(Rows(my_row))
            'If there are no values in the current row (count_row) then do something
            If count_row = 0 Then
                'Delete the current row in the loop (my_row)
                Rows(my_row).Delete
            'End the Count if Statement
            End If
        'Go to the next row on the current sheet
        Next my_row
    'Go to the next sheet in the current workbook
    Next sht
    'Save changes to the workbook and close it
    Workbooks(MyFile).Close savechanges:=True
    'Set file back to Dir
    MyFile = Dir
Loop
'Speeds up code by enabling screen updating (place at end of code)
Application.ScreenUpdating = True`
            },
