'Find the last row of column 1
Dim lrow As Long
lrow = Cells(Rows.Count, 1).End(xlUp).Row
'create a loop from the last row to the first row
For Row_No = lrow To 1 Step -1
    'create variable country_name and make it equal to Column B and the current loop row
    country_name = Range("B" & Row_No)
    'If the value of column B and the current row = "Canada" then do something
    If country_name = "Canada" Then
        'Delete the current row number
        Rows(Row_No).Delete
    'End the if statement
    End If
'Got to next row
Next Row_No`
            }
