  methods: {
    clickHandler(e){
      this.selectTab(e);
      this.showBuilt = !this.showBuilt
    },
    copyToClipboard() {
      // const copyText = document.getElementById("main-area").innerText;
      // copyText.select();
     
              navigator.clipboard.writeText(this.sourcecode).then(function() {
    
                  }, function() {
                    /* clipboard write failed */
                  });
           
    },
    undoDrop() {
      let currentText = document.getElementById("main-area");
      currentText.innerText = this.undoValue;
      this.updateText();
    },
    selectTab(e){
        const elements = document.getElementsByClassName('tab-active');
        [...elements].forEach(element => {
            if(element.classList.contains('tab-active')){
                element.classList.remove('tab-active')
            }
        });

        e.target.classList.add('tab-active');
        

    },
    updateText : 
      debounce(function(){
        const textarea = document.getElementById('main-area');
        this.sourcecode = textarea.innerText
        hljs.highlightBlock(textarea.target);
      },750)
      // this.innerText = e.target.innerText; 
    
    

    
  },
  watch: {
    sourcecode(newVal, oldVal) {
      console.log(`newVal : ${newVal}`, `oldVal : ${oldVal}`)
      if (newVal.length > oldVal.length + 2 && oldVal.length !== 0) {
        this.undoValue = oldVal;
        console.log(this.undoValue)
       
        
      }
    },
    innerText(newVal,oldVal){
      debounce(this.highlightItems,500)
    }
     
    
  },
  created(){
    navigator.permissions.query({name: "clipboard-write"});
  }
  // mounted() {
  //   //starting text for textarea
