  // },
  
};
</script>

<style scoped>
#drag-and-drop {
  display: flex;
  justify-content: center;
  height: 87%;
  margin-top: 5.25rem;
}
.container {
  display: grid;
  grid-template-columns: 5fr 7fr;
  justify-content: space-between;
  width: 1280px;
  height: 100%;
  padding: 0 50px;
}
.boxes-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* flex-wrap:wrap;  */
  height: 750px;
  width: 300px;
  white-space: nowrap;
  overflow: auto;
  padding: 20px;
}
.boxes-wrapper .box {
  margin-right: 40px;
}
.content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.action-buttons-wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 2.8rem;
  width: 884px;
  /* padding: 0 40px; */
}

#main-area {
  resize: none;
  height: 568px;
  width: 788px;
  background-color: #fdfcfd;
  border: 0.5px solid rgba(174, 174, 174, 0.4);
  border-radius: 3px;
  padding: 2rem 3rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1.225rem;
}
.tabs {
  display: flex;
  justify-content: space-between;
  padding: 0px 20px 20px;
  position:relative;
  width:275px
}

@media screen and (max-width:1440px){
  .container{
    width:100%;
    padding: 0 50px;
    display:flex;
  }
  .boxes-wrapper{
    width:auto;
    max-height:50vh;
  }
  #main-area{
    width: 50vw;
    height:50vh;
  }
  .action-buttons-wrapper{
    width:100%;
  }
}

</style>