<template>
  <div id="landing-page">
    <navigation v-bind:class="{blurry: blurState}"></navigation>
    <dragAndDrop v-bind:class="{blurry: blurState}"></dragAndDrop>
    <formComponent v-on-clickaway="closeForms"
      v-bind:formData="formData.login"
      v-if="loginState"></formComponent>
    <formComponent v-on-clickaway="closeForms"
      v-bind:formData="formData.signup"
      v-if="signupState"></formComponent>
     <loginErrorComponent v-if="pleaseLogin"></loginErrorComponent> 
     <subscriptionErrorComponent v-if="subscriptionRequired"></subscriptionErrorComponent>
  </div>
</template>

<script>
import subscriptionErrorComponent from './components/UI_components/subscriptionErrorComponent'
import loginErrorComponent from './components/UI_components/loginErrorComponent'
import { mixin as clickaway } from 'vue-clickaway'
import axios from 'axios'
import {bus} from './main'
import navigation from './components/navigation'
import dragAndDrop from './components/dragAndDrop'
import formComponent from './components/formComponent'

export default {
  name: 'Landing-Page',  
  mixins: [ clickaway ],
  components: {
    navigation,
    dragAndDrop,
    formComponent,
    loginErrorComponent,
    subscriptionErrorComponent
  },
  data(){
    return{
       subscriptionRequired : false,
       pleaseLogin : false,
      //user data is stored here
       userData : '',
       //true if user is logged in
       loggedinState : null,
       //true if log in form is opened
       loginState : false,
       //true if sign up form is open
       signupState : false,
       blurState : false,

       formData : {
         login : {
           title : 'Log In',
           titleSubtext : 'Please log in to use this application',
           inputs : {
             username : {
               label : 'Username',
               type : 'text',
               model: '',
               error: '',
             },
             password : {
               label : 'Password',
               type : 'password',
               model : '',
               error: ''
             }
           },
           buttonData : {
              title: 'Log in',
              method: 'login'
           },
           redirect : {
             text : {
               0: "Don't have an account?",
               1: 'Click here to Sign Up!',
             },
             redirectFunction : 'redirectToSignUp'
           }
         },
         signup : {
           title : 'Sign Up',
           titleSubtext : 'Please enter Your details to Sign Up',
           inputs : {
             username : {
               label : 'Username',
               type : 'text',
               model : '',
               error : '',
             },
             email : {
               label : 'Email',
               type : 'email',
               model : '',
               error : '',
             },
             password : {
               label : 'Password',
               type : 'password',
               model : '',
               error : '',
             },
             confirm : {
               label: 'Confirm Password',
               type: 'password',
               model: '',
               error : '',

             }
           },
           buttonData : {
                title: 'Sign Up',
                method : 'signup'
           },
           redirect : {
             text : {
               0: 'Already have an account?',
               1: 'Click here to Log In!'
             },
             redirectFunction : 'redirectToLogin'
           }
         }
       },
    }
  },
  methods:{
    closeForms(){
      if(this.loginState){
        this.loginState = !this.loginState;
        this.blurState = !this.blurState;
      }else if (this.signupState){
        this.signupState = !this.signupState;
        this.blurState = !this.blurState;
      }
    }
  },
  //initializing listeners for opening forms
  created(){

    // checking if user is authenticated

    
    axios.create({withCredentials:true}).get('https://workchores.herokuapp.com/auth/user')
    .then((res) => {
    //if user is logged in, store user data in a variable and change logged in state

    if(res.data){
      this.userData = res.data;
      this.loggedinState = !this.loggedinState;
    }else{
      this.loggedinState = false;
    }
   


    }).catch(err=>{
      console.log(err);
    })
    bus.$on('loginErrorOpenLogin', ()=>{
      this.pleaseLogin = false;
      this.loginState = !this.loginState;
    });
    bus.$on('loginErrorOpenSignup', ()=>{
      this.pleaseLogin = false;
      this.signupState = !this.signupState;
    });

    bus.$on('dragStarted',()=>{
     console.log('drag event fired')     
          if(!this.userData){
              
              this.pleaseLogin = true;
              this.blurState = !this.blurState;
              bus.$emit('notDraggable');
          }else if(!this.userData.subscription_time){
              
              this.subscriptionRequired = true;
              this.blurState = !this.blurState;
              bus.$emit('notDraggable');
          }else{
            bus.$emit('draggable')
          }
      })




    bus.$on('loginPressed', ()=>{
      //if signup is open, close signup and open login
      if(this.signupState){
        this.loginState = !this.loginState;
        this.signupState = !this.signupState;
      }
      else{
      //else open login and blur screen
      this.loginState = !this.loginState;
      this.blurState = !this.blurState;}
      
    });
    bus.$on('signupPressed', ()=>{
      //if login is open, close login and open signup
      if(this.loginState){
        this.loginState = !this.loginState;
        this.signupState = !this.signupState;
      }else{
      //else open sign up and blur screen
      this.signupState = ! this.signupState;
      this.blurState = !this.blurState;}
      
    });



    //events after log in and sign up
    bus.$on('afterLogIn', (res) =>{
      console.log('afterLogIn')
      //when user logs in, close form and undo blur
      this.loginState = !this.loginState;
      this.blurState = !this.blurState;
    })  
    bus.$on('signedUp', () =>{
      //when user signs up, close form and undo blur
      this.signupState = !this.signupState;
      this.blurState = !this.blurState;
    })  


    
  },
  //destroying listeners for opening forms
  beforeDestroy(){
    bus.$off('loginPressed');
    bus.$off('signupPressed');
    bus.$off('loggedIn');
    bus.$off('signedUp');
    bus.$off('afterLogin');
    bus.$off('dragStarted');
    bus.$off('loginErrorOpenLogin')
  }
}
</script>

<style scoped>

.blurry{
  filter : blur(6px);
}
</style>
