<template>
  <nav id="navigation-bar">
      <div class="container">
        <img @click="goHome" id="logo" :src="require('../assets/logo.png')">
        <div class="button-container">
            <customBtn v-if="showBtns" @click.native="openLogin()" class="menu-btn white" v-bind:buttonData="login"></customBtn>
            <customBtn v-if="showBtns" @click.native="openSignup()" class="menu-btn red" v-bind:buttonData="signup" ></customBtn>

            <p class="greeting-text" @click="toggleDropDown"  v-if="userData">Logged in as {{userData.username | firstCapital}} </p>
            <div class="triangle-down" v-if="userData"></div>
            
            <div @mouseleave="toggleDropDown" v-if="userData && hover" class="user-links">
              <div class="triangle-up"></div>
              <a class="user-link" href="" @click.prevent="goDashboard">Manage Your Account</a>
            </div>
            <customBtn v-if="userData" @click.native="logOut()" class="menu-btn red" v-bind:buttonData="logout" ></customBtn>

        </div>
      </div>
  </nav>
</template>



<script>
import axios from 'axios'
import {bus} from '../main'
import customBtn from './UI_components/button'
export default {
  name: 'WorkChores',
  components: {
    customBtn
  },
  data(){
      return{
          login: {
              title : 'Log In'
          },
          signup: {
              title: 'Sign Up'
          },
          logout : {
            title : 'Log out'
          },
          userData : null,
          showBtns : null,
          hover : false,
      }
  },
  methods: {
    goHome(){
      this.$router.push('/')
    },
    
    openLogin(){
      bus.$emit('loginPressed');
    },
    
    openSignup(){
      bus.$emit('signupPressed');
    },

    logOut(){
      axios.create({withCredentials:true}).get('https://workchores.herokuapp.com/auth/logout')
      .then((res) =>{
        this.userData = null;
        this.showBtns = true;
        this.$router.push('/')
      })
    },
    toggleDropDown(){
      this.hover = !this.hover
    },
    goDashboard(){
      this.$router.push('/user-dashboard');
    }

  },
  filters: {
    firstCapital(value){
      return value.charAt(0).toUpperCase() + value.substring(1)
    }
  },
  created(){
    
      axios.create({withCredentials:true}).get('https://workchores.herokuapp.com/auth/user')
      .then((res)=>{
        if(res.data){
          bus.$emit('transferData', res.data);
          this.userData = res.data;
          this.showBtns = false;
        }else{
          this.showBtns = true;
        }
      }).catch(err =>{
        this.showBtns = true;
        this.$router.push('/')
      })
    

      bus.$on('afterLogIn', (res)=>{
        console.log('navigation component received event')
        //when user logs in, rerender navigation bar
        this.userData = res.data;
        this.showBtns = false;
      })

     

  },
  beforeDestroy(){
     bus.$off('loggedIn');
  }
  
}
</script>

<style scoped>
  nav{
      height:75px;
      display:flex;
      justify-content: center;
      

  }
  .container{
      display:flex;
      justify-content: space-between;
      align-items:center;
      width:1280px;
      height:75px;
      padding:0 50px;
      
  }
  #logo{
      height:45px;
      cursor:pointer;
    }
  .menu-btn.red{
      margin-left:2rem;
  }
  .button-container{
    display:flex;
    align-items:center;
  }
  .greeting-text{
    font-size: 1.6775rem;
    font-weight: 700;
    cursor: pointer;
    user-select: none;
  }
  
  .user-links{
    position:absolute;
    padding: .65rem 0rem;
    border-top: none;
    top:60px;
    background-color: #FDFCFD;
    border: .5px solid #EAEAEA;
    margin-top: .3rem;
    border-radius:3px;
  }
  .user-links:hover{
    background-color : rgb(255, 255, 255);
    transition: .3s;
  }
  .user-link{
    text-decoration: none;
    color:black;
    padding:4rem 3rem
    
  }
  .triangle-up{
  width: 0; 
  height: 0; 
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  
  border-bottom: 6px solid rgb(238, 238, 238);
  position:absolute;
  top: -5.5px;
  z-index: 9999;
  right:8px;
  
  }
  .triangle-down {
  width: 0; 
  height: 0; 
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  
  border-top: 6px solid black;
  margin-left: .5rem;
  margin-top: .5rem;
}
</style>