<template>

    <div class="subBox" >
        <a href=""  :draggable="subDraggable" class="small-box"
        @dragstart="dragStart($event, smallBox.value)"
        @dragend="deselectText"
        @click="openMini($event)" >
            <p >{{smallBox.title}}</p>
        </a>
    
        <div v-if="openMiniBox" class="miniBoxes">
        <a href="" v-for="mini in smallBox.miniBoxes" :key="mini.id" :draggable="miniDraggable" class="mini-box"
        @dragstart="dragStart($event, mini.value)"
        @dragend="deselectText" >
            <p >{{mini.title}}</p>
        </a>
        </div>
    </div>
 
</template>

<script>
import {bus} from '../../../main'

export default {
  props: ['smallBox', 'isDraggable'],
  data(){
    return {
      openMiniBox : false,
      subDraggable : null,
      miniDraggable : true,
    }
  },
  
  methods : {
    

    openMini (e){
      
      e.preventDefault();
      if(this.smallBox.miniBoxes){
        bus.$emit('closeOtherSmallBoxes')
        this.openMiniBox = !this.openMiniBox;
      }
    },
    dragStart(e,value){
      
      bus.$emit('dragStarted');
      e.dataTransfer.setData("text/html", value); 

    },
    deselectText(){
      if(window.getSelection()){
        window.getSelection().empty();
      }else if (document.getSelection()){
        document.getSelection().empty;
      }
    },
    
    
  },
  created(){
    

    if(this.smallBox.value){
      this.subDraggable = true;
    }else{
      this.subDraggable = false;
    }
    bus.$on('draggable', ()=>{
      this.subDraggable = true;
      this.miniDraggable= true;
    })
    bus.$on('notDraggable', ()=>{
      this.subDraggable = false;
      this.miniDraggable =false;
    });
    bus.$on('closeOtherSmallBoxes', ()=> {
      this.openMiniBox = false;
    })
  },
  beforeDestroy(){
    bus.$off('draggable');
    bus.$off('notDraggable');
    bus.$off('closeOtherSmallBoxes')
  }
  
}
</script>


<style scoped>
  .subBox{
    display:flex;
    flex-direction: column;
    align-items:center;
  }
  .small-box{
    display:flex;
    align-items:center;
    justify-content:center;
    background-color:#FDFCFD;
    border: 1px #DCDCDC solid;
    border-radius:3px;
    margin-bottom: 1.56rem;
    width: 172px;
    height: 50px;
    color:black;
     font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
      font-size: 1rem;
      text-decoration: none;
  }
  .small-box:hover{
    transition : color 0s;
    transition : background-color .7s;
    background-color: #B50002;
   color:white;
  }
  .subBoxes{
    display:flex;
    flex-direction: column;
    align-items:center;
    width:215px;
  }
  .miniBoxes{
    display:flex;
    flex-direction: column;
    align-items:center;
    width:215px;
    margin-right:0;
  }
  .mini-box{
    display:flex;
    align-items:center;
    justify-content:center;
    background-color:#FDFCFD;
    border: 1px #C4C3C3 solid;
    margin-bottom: 1.56rem;
    width: 140px;
    height: 30px;
    color:black;
     font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
      font-size: 1rem;
    text-decoration:none;
  }
  .mini-box:hover{
    transition : color 0s;
    transition : background-color .7s;
    background-color: #B50002;
   color:white;
  }
  p{
    white-space:normal;
    text-align:center;
  }

</style>