<template>
  <div id="error-component">
     <h1 class="error-title">Please Log In</h1>
     <p class="title-subtext">You have to be logged in to use this application</p> 
     <hr class="underline">
     <div class="buttons">
      <customBtn class="white prompt-btn" v-bind:buttonData="login" @click.native="openLogin()"></customBtn>
      <customBtn class="red prompt-btn" v-bind:buttonData="signup" @click.native="openSignUp()"></customBtn>
    </div>
  </div>
</template>

<script>
import customBtn from './button'
import {bus} from '../../main'
export default {

  components: {
    customBtn
  },
  data(){
      return{
          login: {
              title : 'Log In'
          },
          signup: {
              title: 'Sign Up'
          },
          }
      
  },
  methods:{
      openLogin(){
        bus.$emit('loginErrorOpenLogin');
      },
      openSignUp(){
        bus.$emit('loginErrorOpenSignup')
      }
      
  },

 
 
 
}
</script>

<style scoped>
#error-component{
  display:flex;
  flex-direction: column;
  align-items:center;
  width: 480px;
  border: 1px solid #D9D9D9;
    border-radius:6px;
    background-color: #FFFFFF;
    position: absolute;
    top: 7vh;
    left: 0;
    right:0;
    margin-left:auto;
    margin-right:auto;
}
.error-title{
    font-size:2rem;
    font-weight:700;
    margin: 2.2675rem 0 0.83rem;
}
.title-subtext{
  margin:0;
  color: rgba(199,196,196,0.7);
  display:inline;
  
}
.underline{
  width: 23.75rem;
  margin-top: 0.875rem;
  border: .5px solid rgba(174,174,174,0.2)
 
}
.buttons{
  width:381px;
  display:flex;
  justify-content: space-between;
  margin-top: 1.1875rem;
  margin-bottom: 2.9375rem;
}
.buttons * {
  margin: 0rem 1.375rem;
}

</style>