<template>
  <div id="manage-payments">
    <div class="content-wrapper" v-bind:class="{blurry: blurState}">
        <h1 class="payments-title">Manage Payments</h1>
        <hr class="title-hr"/>
        <div class="payments-content">
            <p v-if="userData.subscription_time" class="subscription-text">Your Subscription is active until {{userData.subscription_time | dateFilter }}</p>
            <p  v-if="!userData.subscription_time" class="extend-subscription"> Extend Subscription : </p>
            <div v-if="!userData.subscription_time" class="subscription-items">
                <div class="subscription-item">
                    <label  class="input-wrapper" >1 month subscription, $9.99
                        <input v-model="plan" class="subscription-input" type="radio" name="duration" checked="checked" value="month" >
                        <span class="radio-replace"></span>
                    
                    </label>
                </div>
                <div class="subscription-item">
                    <label  class="input-wrapper" >1 year subscription, $99.99
                        <input v-model="plan" class="subscription-input" type="radio" name="duration" value="year" >
                        <span class="radio-replace"></span>
                    
                    </label>
                </div>
                

            </div>
            <div class="checkout-btns">
                <customBtn v-if="!userData.subscription_time" @click.native="openStripe" class="red checkout-btn payments-btn" v-bind:buttonData="button"></customBtn>
                <button v-if="!userData.subscription_time" class="paypal-btn" >
                    <img @click="payWithPaypal" 
                    src="http://www.paypalobjects.com/webstatic/en_US/i/buttons/checkout-logo-large.png"
                    alt="Check out with PayPal" />
                    </button>
            </div>
            <div v-if="userData.subscription_time" class="cancel-btn" @click="cancelSub">Cancel Your Subscription</div>
            <p v-if="subscription_time_error" class="subscription-error">Your subscription is already active</p>
            <p v-if="cancellation_message" class="subscription-error">{{cancellation_message}}</p>
            
        </div>
    </div> 
    <stripeForm v-bind:plan="plan" v-if="showStripe"  v-on-clickaway="openStripe"></stripeForm>
<springSpinner v-if="spinnerActive" id="spinner"
  :animation-duration="3000"
  :size="60"
  color="#B50002"
/>
  </div>
</template>

<script>
import { SpringSpinner } from 'epic-spinners'
import { mixin as clickaway } from 'vue-clickaway';
import stripeForm from './stripeForm'
import {bus} from '../main'
import navigation from './navigation'
import customBtn from './UI_components/button'
import axios from 'axios'

export default {
  mixins: [ clickaway ], 
  name: 'Manage-Payments',  
  components : {
      customBtn,
      stripeForm,
      SpringSpinner
  },
  data(){
      return{
         button:{
             title : 'Proceed to Checkout',
             
         },
         showStripe: false,
         blurState : false,
         userData : '' ,
         spinnerActive : false,
         plan: 'year',
         subscription_time_error : null,
         cancellation_message : '',
      }
  },
  methods : {
      payWithPaypal () {
        //   if(this.userData.subscription_time){
        //       this.subscription_time_error = true;
        //       return
        //   }

          const headers = {
              'Content-Type': 'application/json',
          }
          axios.post('https://workchores.herokuapp.com/payment/paypal',  {id  : this.userData.user_id, plan: this.plan})
          .then((res) =>{
              window.location = res.data;
          });

          this.spinnerActive = true;
          this.blurState = true;
          bus.$emit('blurItems');  
      },
      cancelSub () {
          this.spinnerActive = true;
          this.blurState = true;
          const headers = {
              'Content-Type': 'application/json',
          }
          axios.post('https://workchores.herokuapp.com/subscriptions/cancel',  {id  : this.userData.user_id})
          .then(res=> {
              this.cancellation_message = res.data
              this.spinnerActive = false;
              this.blurState = false;
            
              } )

         
      },
      openStripe() {
        //   if(this.userData.subscription_time){
        //       this.subscription_time_error = true;
        //       return
        //   }

          this.showStripe = !this.showStripe;
          this.blurState = !this.blurState;
          bus.$emit('blurItems')
      }
  },
  filters : {
      dateFilter(value){
          if(!value){
              return '';
          }else{
              console.log(value);
              let cutDate = value.substring(0,10) ;
              return cutDate
          }
      }
  },
  created(){
    //   getting data from nav component and displaying it in this component.thought this app would be too
    //   small for vuex ...

     bus.$on('transferData', (data)=>{
         if(data){
             this.userData = data
         }else{
             this.$router.push('/')
         }
     });

     bus.$on('payingWithStripe', ()=>{
         this.spinnerActive =true;
     })
  },
  beforeDestroy(){
      bus.$off('transferData');
      bus.$off('payingWithStripe');
  }
  
}

</script>

<style scoped>
.payments-title{
    font-size:2rem;
    font-weight:400;
}
.title-hr{
    height:1px;
    background-color: #E2DEDE;
    border: 0px;
    margin-bottom: 1.9rem;
    
}
.subscription-text{
    font-size:1.1rem;
    margin:0;
}
.extend-subscription{
    font-size:1.1rem;
    margin:0;
    margin-top: 2.8125rem;
}


.subscription-item{
margin-top:1.25rem;
}
.input-wrapper{
    
    position:relative;
    cursor:pointer;
    user-select:none;
    padding-left:1.9rem;
    
}   

.input-wrapper input{
    display:none;
}
.radio-replace{
    display:inline-block;
    width: 17px;
    height:17px;
    background-color:#FFFFFF;
    border: 1px solid #CBCBCB;
    position:absolute;
    left: 0;
    top: 2px;
    border-radius:50%;

}
.input-wrapper:hover .radio-replace{
    background-color: rgb(241, 241, 241);
    transition : .3s;
}
.input-wrapper input:checked + .radio-replace:after{
    content: "";
    width: 8px;
    height: 8px;
    background-color: #B50002;
    border: 0px;
    position:absolute;
    border-radius:50%;
    top:50%;
    left:50%;
    transform: translate(-54%, -55%)

}
.checkout-btns{
    display:flex;
    align-items:flex-end;
}
.payments-btn{
    margin-top: 2.31rem;
}
.paypal-btn{
    background-color:transparent;
    border: none;
    margin-left: 3rem;
    height:42px;
    cursor: pointer;
}
.paypal-btn:focus{
    outline: none;
}

.paypal-btn img {
    height:40px;
}
.blurry{
  filter : blur(6px);
}
#spinner{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    margin: auto;
}
.subscription-error{
    color: #B50002;
}
.cancel-btn{
    margin-top:20px;
    padding: 20px 0; 
    color: #B50002;
    cursor:pointer;
}
</style>
