<template>
<div class="box">
  <a href="" :draggable="isDraggable" class="dropable-box" @click="openUp($event)" @dragstart="dragStart($event, boxData.value)"
  @dragend="deselectText($event)">
     <p class="box-text">{{boxData.title}}</p>
  </a>
  <div v-if="openSubBox" class="sub-box-wrapper">
    <subBox v-for="smallBox in boxData.subBoxes" :key="smallBox.id"  v-bind:smallBox="smallBox" v-bind:isDraggable="isDraggable" ></subBox> 
  </div>
 </div>
  
</template>

<script>
import {bus} from '../../main'
import subBox from './subcomponents/subBox'


export default {
  components:{
    subBox
  },
  props:['boxData'],
  data (){
    return {
      openSubBox : false,
      
      isDraggable : null,
     
    }
  },
  methods :{
    
    

    //when the text is selected it doesnt let people hover another item over it
    dragStart(e,value){
      console.log(value)
      bus.$emit('dragStarted');
      e.dataTransfer.setData("text/html", value); 
      
    },
    deselectText(e){


      if(window.getSelection()){
        window.getSelection().empty();
      }else if (document.getSelection()){
        document.getSelection().empty;
      }
    },
    openUp(e) {
      e.preventDefault();

      if(this.boxData.subBoxes){
        bus.$emit('closeOtherBigBoxes');
        this.openSubBox = !this.openSubBox; 
      }
    },
    
  },
  created(){
    bus.$on('draggable', ()=>{
      this.isDraggable = true;
    })
    bus.$on('notDraggable', ()=>{
      this.isDraggable = false;
    })
    bus.$on('closeOtherBigBoxes', ()=>{
      this.openSubBox = false;
    })
    if(this.boxData.value !== ''){
      this.isDraggable = true;
    }else{
      this.isDraggable = false
    }
  },
  beforeDestroy(){
    bus.$off('draggable');
    bus.$off('notDraggable');
    bus.$off('closeOtherBigBoxes');
  }
}

</script>

<style scoped>
  .dropable-box{
      display:flex;
      align-items:center;
      justify-content:center;
      width: 215px;
      height:50px;
      background-color:#FDFCFD;
      border: 1px #F1F1F1 solid;
      margin-bottom: 1.56rem;
      color:black;
      /* CSS UNHOVER */
      transition: color 0s;
      transition : background-color .5s;
  }
  .dropable-box:hover{
    transition : color 0s;
    transition : background-color .7s;
    background-color: #B50002;
   color:white;
  }
  a{
    text-decoration: none;
  }
  .box-text{
      
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
      font-size: 1.225rem;
      
      
  }
  
  
</style>