<template>
  <div id='stripe-form' v-bind:class="{blurry: blurState}">
    <h1 class="form-title">Get Started!</h1>
   <div class="form-inputs"> 
        <label for="" class="input-label">Name</label>
        <input type="text" class="form-input">
        <label for="" class="input-label">Surname</label>
        <input type="text" class="form-input">
        <label for="" class="input-label">Credit Card Information</label>
    </div>  
    
<div class="stripe-input">
   
    <card class='stripe-card'
      :class='{ complete }'
      stripe='pk_live_YHyzVFrpHKjkqG89LbtNlPrz00bsCbYG0h'
      :options='stripeOptions'
      @change='complete = $event.complete'
    />

</div>
    <customBtn v-bind:buttonData='button' class='red form-btn stripe-btn' @click.native='pay' :disabled='!complete'></customBtn>
  </div>
</template>

<script>
import {bus} from '../main'
import axios from 'axios'
import { Card, createToken } from 'vue-stripe-elements-plus'
import customBtn from './UI_components/button'
export default {
  props : ['plan'],
  data () {
    return {
      blurState : false,
      userData : '',
      complete: false,
      stripeOptions: {
        // see https://stripe.com/docs/stripe.js#element-options for details
        style : {
            base: {
                fontFamily : 'Segoe UI'
            }
        }
      },
      button : {
          title : 'Subscribe!'
      }
    }
  },

  components: { Card,customBtn },

  methods: {
    pay () {
      this.blurState = true;
      bus.$emit('payingWithStripe')
      // createToken returns a Promise which resolves in a result object with
      // either a token or an error key.
      // See https://stripe.com/docs/api#tokens for the token object.
      // See https://stripe.com/docs/api#errors for the error object.
      // More general https://stripe.com/docs/stripe.js#stripe-create-token.
      createToken().then(data => {
        console.log(this.userData._id);
        axios.post('https://workchores.herokuapp.com/payment/stripe', {cardId : data.token.card.id, tokenId : data.token.id, userId: this.userData.user_id, plan:this.plan})
        .then((res)=>{
          window.location = 'https://www.workchores.com/user-dashboard'
        })
      })
    }
  },
  mounted(){
    //   getting data from nav component and displaying it in this component.thought this app would be too
    //   small for vuex ...
    
     axios.create({withCredentials:true}).get('https://workchores.herokuapp.com/auth/user')
      .then((res)=>{
        if(res.data){
          
          this.userData = res.data;
          console.log(this.userData)
        }
      })
  },
  
}
</script>

<style scoped>
#stripe-form{
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content: center;
    width: 22.25rem;
    height:auto;
    
    border: 1px solid #D9D9D9;
    border-radius:6px;
    background-color: #FFFFFF;
    position:absolute;
    top:12vh;
    margin-left:auto;
    margin-right:auto;
    right:0;
    left:0;

}
.form-title{
    font-size: 2rem;
    font-weight:700;
    margin: 3.43rem 0 1.625rem 0 ;
}
.form-inputs{
    display:flex;
    flex-direction: column;
    
}
.stripe-card {
  
    width: 14.225rem;
    height:1.075rem;
    border: 1px solid #F1F1F1;
    border-radius: 3px;
    background-color:#FDFCFD;
    padding-left:.8rem;
    padding: .8rem .8rem .8rem;
   
  
}
.stripe-card.complete {
  border-color: green;
}
.form-input{
    width: 14.825rem;
    height:1.875rem;
    border: 1px solid #F1F1F1;
    border-radius: 3px;
    background-color:#FDFCFD;
    padding-left:.8rem;
    margin-bottom: 1.25rem;
    -webkit-appearance: none;
}
.input-label{
    font-size: .55rem;
    margin-bottom: .5rem;
};
.blurry{
  filter : blur(6px);
}
</style>