<template>
  <div id="user-dashboard">
    <navigation v-bind:class="{blurry: blurState}"></navigation>
    <div class="container">
        <sideMenu v-bind:class="{blurry: blurState}"></sideMenu>
        <managePayments></managePayments>
    </div>
  </div>
</template>

<script>

import navigation from './components/navigation'
import sideMenu from './components/UI_components/sidemenu'
import managePayments from './components/managePayments'
import {bus} from './main'

export default {
  name: 'User-Dashboard',  
  components : {
      navigation,
      sideMenu,
      managePayments
  },
  data(){
    return{
      blurState : false
    }
  }, 
  created(){
    bus.$on('blurItems',()=>{
      this.blurState =!this.blurState
    })
  },
  beforeDestroy(){
    bus.$off('blurItems');
  }
}
</script>

<style scoped>
#user-dashboard{
    display:flex;
    flex-direction: column;
    align-items:center;
}
.container{
    width:1280px;
    margin-top: 5.625rem;
    display:grid;
    grid-template-columns: 2fr 8fr;
}
.blurry{
  filter : blur(6px);
}
</style>
