<template>
  <div class="form-component">
    <form class="form-wrapper" action="">
        <h1 class="form-title">{{formData.title}}</h1>
        <p class="form-title-subtext">{{formData.titleSubtext}}</p>
        <div v-for="input in formData.inputs" :key="input.id" id="form-inputs">
            <label  for="" class="form-label">{{input.label}}</label>
            
            <input @input="removeErrors" class="form-input" :type="input.type" v-model='input.model' />
            <p v-if="input.error.length>0" class="error-message">{{input.error}}</p>
        </div>
            
        <p v-if="loginError.length>0" class="error-message">{{loginError[0]}}</p>
        <customBtn @click.native.prevent="handleMethod(formData.buttonData.method)" class="red form-btn" v-bind:buttonData="formData.buttonData" ></customBtn>
        <div class="form-link-container">
        <a v-for="text in formData.redirect.text" :key="text.id" class="form-link-text"  href=""
        @click.prevent="redirectMethod(formData.redirect.redirectFunction)">{{text}}</a>
        </div>
    </form>
  </div>
</template>



<script>
import {bus} from '../main'
import axios from 'axios'
import customBtn from './UI_components/button'

export default {
  props: ['formData'],
  name: 'Form',
  components : {
      customBtn,
  },
  data(){
     return {
         loginError : [],
         signUpError : [],
         
     } 
  },
  methods: {
      redirectMethod(funcName){
          //handling dynamic event
          this[funcName]();

      },
      redirectToSignUp(){
          //open signup form
          bus.$emit('signupPressed');
      },
      redirectToLogin(){
          //open login form
          bus.$emit('loginPressed');
      },


      //handle request methods
      handleMethod(funcName){
          this[funcName]()
      },
      
      
      removeErrors(){
          //when corrections to previously entered credentials are made, remove any log in errors.
          this.loginError = [];
          
          //very ugly but simple and it works
          if(this.formData.title=== "Sign Up")  
          {this.formData.inputs.username.error = "";
          this.formData.inputs.email.error = "";
          this.formData.inputs.password.error = ""
          this.formData.inputs.confirm.error = ""}
      },  


      //login method

      login(){
          
         const username = this.formData.inputs.username.model;
         const password = this.formData.inputs.password.model;
         

         //if there are errors, dont make additional requests until corrections are made
         if(this.loginError.length === 0){
            axios.create({withCredentials:true})
            .post('https://workchores.herokuapp.com/auth/login', {
                username : username,
                password: password,
            }).then((res)=>{
                //this event closes login form and rerenders navbar buttons and greeting text
                bus.$emit('afterLogIn',res);
                this.$router.go('/')
                
                
            }).catch(err => {
                // if there is an error, display in on screen
                const errorMsg = err.response.data[2];
                this.loginError.push(errorMsg);
               
         });}
         
      },

      signup(){
          const username = this.formData.inputs.username.model;
          const email = this.formData.inputs.email.model;
          const password = this.formData.inputs.password.model;
          const confirmPassword = this.formData.inputs.confirm.model;

          //clearing previous errors  
          this.signUpError =[]
            


          if(!username){
              this.formData.inputs.username.error = "Please enter a username!";
              this.signUpError.push(this.formData.inputs.username.error)
          }

          const emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          if(!emailRegex.test(email)){
              this.formData.inputs.email.error = "Please enter a valid email!";
              this.signUpError.push(this.formData.inputs.email.error)
          }
          
          if(password!==confirmPassword){
              this.formData.inputs.password.error = "Passwords don't match";
              this.formData.inputs.confirm.error = "Passwords don't match";
              this.signUpError.push(this.formData.inputs.password.error);

          }
          if(this.signUpError.length === 0)  
          {
            axios.create({withCredentials:true})
            .post('https://workchores.herokuapp.com/auth/signup',{
                username : username,
                email : email,
                password : password,
            }).then((res)=>{
                
                bus.$emit('signedUp')

            }).catch(err => this.formData.inputs.username.error = err.response.data.message)
            }
      }
  }
}
</script>

<style scoped>
/* styling these form elements only works when they are up here.....*/
.error-message{
    color: #F86161;
    font-size: .7rem;
}

.form-link-text{
    color : #CBC8C8;
    
}
.form-link-container{
    display:flex;
    flex-direction:column;
    align-items:center;
    margin-bottom : 3.43rem
}
.form-input{
    width: 14.825rem;
    height:1.875rem;
    border: 1px solid #F1F1F1;
    border-radius: 3px;
    background-color:#FDFCFD;
    padding-left:.8rem;
    -webkit-appearance: none;
}
.form-component{
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    top:7vh;
    width: 21.25rem;
}
.form-wrapper{
    
    display:flex;
    flex-direction:column;
    align-items:center;
    width: 22.25rem;
    height:auto;
    border: 1px solid #D9D9D9;
    border-radius:6px;
    background-color: #FFFFFF;

}
.form-title{
    font-size: 2rem;
    font-weight:700;
    margin: 3.43rem 0 1.625rem 0 ;
}
.form-title-subtext{
    margin-top: 0;
    margin-bottom: 2.5rem;
    color: #C7C4C4;
}
#form-inputs{
    display:flex;
    flex-direction: column;
    margin-bottom: 1.25rem;
}
.form-label{
    font-size: .55rem;
    margin-bottom: .8rem;
};

</style>
